
.page-Header {
  width: 100%;
  overflow: hidden;
  .page-Header-inner {
    width: 100%;
  }
}

.vegas {
  position: relative;
  &::before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.78);
    z-index: 1;
  }
}

.background {

  &:nth-of-type(2) {
    background: url(../images/bg2.jpg) no-repeat center;
    background-size: cover;
  }

  &::before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.51);
    z-index: 1;
  }
}

.colum-medium {
  color: $isDrkgray;
  background: #e6e7e8;
  text-align: center;
  h1 {
    p {
      padding: 52px 20px 10px 20px;
      color: #58595b;
      font-family: 'BaskervilleBT-Roman';
      font-size: 26px;
      margin: 0;
      line-height: 120%;
      min-height: 218px!important;
    }
  }
}

