.modal-dialog {
  max-width: 70%;
  h6 {
    margin: 1em 0;
  }
}

.modal-header {
  padding-bottom: 30px;
  padding-top: 30px;
  text-align: center;
  position: relative;
  border: none;
  &::after {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    background: rgba(0,0,0, 0.6);
  }
}

.modal-title {
  width: 100%;
  font-size: 3rem;
  color: $isWhite;
  z-index: 10;
}

.close {
  outline: none;
  font-size: 2rem;
  position: relative;
  color: $isWhite;
  z-index: 10;
  opacity: 1;
  &:hover,
  &:focus {
    color: $isWhite;
    opacity: 1;
    outline: none;
  }
}

.services {
  .service-item {
    padding-bottom: 15px;
    padding-top: 15px;
    .item-inner {
      // background: rgba($isLtgray, 0.1);
      overflow: hidden;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
      .service-type {
        text-align: center;
        padding: 0 15px;
        padding-bottom: 15px;
        h5 {
          font-size: 1.3rem;
          padding: 5px;
          margin: 10px 0;
        }
      }
    }
  }
}
