.management {
  background: url('../images/market.jpg') no-repeat center;
  background-size: cover;
  position: relative;
  padding: 100px 0;
  &:after {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.9);
  }

  .title {
    h1 {
      color: $isWhite;
    }
  }

  p {
    color: $isLtwhite;
    font-weight: 100;
  }
}
