#map,
#geo {
    height: 100%;
}

.social-link {
    position: absolute;
    right: 0;
    top: 5px;
    font-size: 1.5rem;
    color: #6b6b6b;
}

.footer {
    width: 100%;
    clear: both;
    padding: 12px 0;
    color: $isLtgray;
    background: #e6e7e8;
    .footer-nav {
        margin: 0;
        padding: 0;
        position: relative;
        .footer-nav-item {
            display: inline-block;
            a {
                color: #535454;
                font-weight: 400;;
                span {
                    color: #a4a7ab;
                }
            }
        }
    }
}

.sec-footer {
    padding: 15px;
    background: #e6e7e8;
    small {
        display: block;
        color: #6b6b6b;
        text-align: center;
        font-size: .7rem;
    }
}