.contact {
    //position: relative;
    &:hover .address {
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
    }
    .address {
        display: block;
        width: 50%;
        height: 500px;
        position: absolute;
        z-index: 15;
        bottom: 45px;
        left: -14px;
        background: #fafbfb;
        text-align: center;
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
        opacity: 0;
        visibility: hidden;
        transform: translateY(-30px);
        transition: all ease 250ms;
        &::after {
            content: '';
            width: 20px;
            height: 20px;
            position: absolute;
            border-bottom-right-radius: 5px;
            bottom: -10px;
            transform: rotate(45deg);
            background: #fafbfb;
            right: 70px;
        }
        .addr-header {
            width: 100%;
            height: 150px;
            background: #f1f1f1;
            border-bottom: 3px solid #41332a;
        }
        .addr-info {
            position: relative;
            z-index: 10;
            overflow: hidden;
            .addr-info-inner {
                transform: translateY(0);
                transition: all ease 250ms;
                overflow: hidden;
                height: 335px;
                &.movetop {
                    transform: translateY(-88.6%);
                }

                .addr-title {
                    padding: 15px 0;
                    text-align: center;
                }

                .geo-widget {
                    width: 100%;
                    height: 295px;
                }

                .add-loc {
                    display: block;
                    margin-bottom: 1em;
                    i {
                        width: 100%;
                        height: 16px;
                        display: block;
                        font-size: 1.2em;
                        margin-bottom: .5em;
                        color: #8c8c8c;
                    }
                }
            }
            
        }
    }
}
