.bottom {
  width: 100%;
  display: block;
  padding: 15px;
  text-align: center;
  outline: none;
  &.default {
    background: #e6e7e8;
  }
}
a.bottom {
  font-weight: 600;
  color: #6b6b6b;
  text-decoration: none;
  &:hover {
    text-decoration: none;
  }
}

.custom-btn {
  border: none;
  cursor: pointer;
  -webkit-font-smoothing: antialiased;
  font-weight: 500;
  outline: none;
  -webkit-appearance: none;
  border-radius: 7px;
  width: 100%;
  transition: all .2s ease;
  position: relative;
  text-align: center;
  line-height: 1em;
  outline: none;
}

.button-blue {
  padding: 18px 30px 18px;
  font-size: 1em;
  background: #0d9a9a;
  font-weight: 500;
  color: $isWhite;
  position: relative;
  &:focus {
    outline: none;
  }
}

.findMe {
  width: 100%;
    padding: 8px;
    display: block;
    background: #f1f1f1;
    font-weight: 600;
    &:hover,
    &:focus {
      text-decoration: none;
    }
}