body {
  font: 12px/1.5 'Open Sans', 'Segoe UI', Arial, sans-serif;
  font-weight: 100;
  color: #6b6b6b;
}

h1,h2,h3,h4,h5,h6 {
  font-family: 'Gotham-Medium','Anton', sans-serif;
}

h1,h2,h3,h4,h5,h6 {
  margin: 0;
}

p {
  color: $isLtgray;
  font-size: .9rem;
  font-weight: 400;
}

em {
    font-size: 1em;
    padding: 70px 0;
    display: block;
    font-weight: 500;
}

* img {
  width: 100%;
}

h2 {
  font-size: 1rem;
}

.no-padding {
  padding: 0;
}

.no-marging {
  margin: 0;
}

.title {
  text-align: center;
  padding: 80px 0;
  position: relative;
  z-index: 10;
  background:  url(../images/vegas-2.jpg) no-repeat center;
  background-size: cover;
  &::after {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.58);
    z-index: 1;
  }
  h1 {
    color: $isWhite;
    position: relative;
    display: inline;
    z-index: 10;
    &:after {
      content: '';
      width: 50px;
      height: 4px;
      position: absolute;
      left: 0;
      bottom: -5px;
      background: $isDrkgreen;
    }
  }
}

.sub-title {
  margin: 15px 0;
  text-align: center;
}

.custom-list {
  margin: 10px 0;
  li {
    list-style: none;
    font-size: 1rem;
    color: $isLtgray;
    &:before {
      content: '\f058';
      font-family: 'FontAwesome';
      margin-right: 8px;
      color: #7b7b7b;
    }
  }
}

.sub-content {
  padding: 50px 0;
}

.caption {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  z-index: 5;
  text-align: center;
  padding: 0 5px;
  h2 {
    a {
      color: $isWhite;
      text-transform: uppercase;
      text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.64);
      &:hover,
      &:focus {
        text-decoration: none;
      }
    }
  }
}

.colum-wide,
.colum-medium,
.colum-narrow {
  width: 100%;
}

.colum-wide {
  height: 62.55vh;
  background: #dedede;
}

.colum-medium {
  height: 36vh;
}

.colum-narrow {
  height: 26.5vh;
  background: #ffffff;
}

.table td {
  font-size: 1rem;
}

tbody tr:nth-of-type(even) {
  background: #eceeef;
}

tbody tr td:nth-of-type(1) {
  font-weight: 500;
}

.page-window {
  max-width: 1170px;;
  margin: 0 auto;
  overflow: hidden;
  //box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2);
}


.message {
  display: none;
  width: 25%;
  height: 400px;
  position: fixed;
  bottom: 0;
  right: 3em;
  z-index: 20;
  background: $isWhite;
  transform: translateY(366px);
  transition: all cubic-bezier(0.65, 0.05, 0.36, 1) 200ms;
  &.is-showing {
    transform: translateY(0);
  }
  .msg-header {
    width: 100%;
    background: #0d9a9a;
    a {
      display: block;
      padding: 8px 0;
      color: $isWhite;
      text-align: center;
      font-weight: 600;
      &:hover,
      &:focus {
        text-decoration: none;
      }
    }
  }
  .msg-body {
    padding: 15px 15px;
    .form-control{
      border: 1px solid #d3dce6;
      padding-left: 35px;

      &.danger-returned  {
       border: 1px solid #ff002a;
      }

      &.danger-returned .form-group i  {
       color: #ff002a;
      }
    }

    label {
      display: block;
      margin-bottom: 10px;
      line-height: 14px;
      font-size: 13px;
      font-weight: 500;
      position: relative;
      color: #9fa6ad;
    }

    textarea {
      padding-left: 10px !important;
    }
    
    ::-webkit-input-placeholder {
      font-size: 0.7rem;
      color: #9fa6ad;
    }

    .form-group {
      font-size: .7rem;
      i {
        width: 16px;
        height: 16px;
        position: absolute;
        z-index: 15;
        font-size: 1rem;
        top: 50%;
        left: 50%;
        transform: translate(-900%, -50%);
        color: #d3dce6;
      }
      &:focus i  {
        color: red;
      }
    }
  }
}

.spinner {
    display: none;
    width: 20px;
    height: 20px;
    border-width: 2px;
    border-style: solid;
    border-color: #fff transparent transparent;
    border-radius: 50%;
    animation: spin 1s linear infinite;
    box-sizing: border-box;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -10px -10px;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@-moz-keyframes spin {
  0% {
    -moz-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -moz-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@-ms-keyframes spin {
  0% {
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -ms-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@-o-keyframes spin {
  0% {
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}