.bg-faded {
    background: #ffffff;
    border-radius: 0;
    color: rgba(255, 255, 255, 0.5);
}

.navbar-brand {
    color: rgb(107, 107, 107) !important;
    font-weight: 800;
}

.navbar-light .navbar-nav .nav-link {
    color: #bdbdbd;
}

.navbar-inverse .navbar-nav .active>.nav-link, .navbar-inverse .navbar-nav .nav-link.active, .navbar-inverse .navbar-nav .nav-link.open, .navbar-inverse .navbar-nav .open>.nav-link {
    color: #535454;
}

.bg-inverse {
    background-color: #ffffff  !important;
}

.navbar-inverse .navbar-nav .nav-link {
    color: rgb(83, 84, 84);
    font-weight: 500;
}

.navbar-wrapper {
    position: relative;
    .navbar-item.brand {
        width: 145px;
        height: 100px;
        position: absolute;
        top: 0;
        left: 0;
        padding: 7px;
        background: #ffffff;
        h1 {
            font-family: "Palatino Linotype", serif;
            font-size: 65px;
            line-height: 1.34em;
        }
    }
    .navbar-item:nth-of-type(2) {
        padding-left: 145px;
    }
}

.top-navbar {
    ul {
        li {
            .nav-link {
                position: relative;
                font-weight: 500;
                &::after {
                    content: '/';
                    position: absolute;
                    right: 0;
                }
            }
        }
    }
}